<template>
  <div class="attr-list">
    <el-form>
      <el-form-item
        v-for="(key, index) in styleKeys"
        :key="index"
        :label="map[key]"
      >
        <el-color-picker
          v-if="key == 'borderColor'"
          v-model="curComponent.style[key]"
        ></el-color-picker>
        <el-color-picker
          v-else-if="key == 'color'"
          v-model="curComponent.style[key]"
        ></el-color-picker>
        <el-color-picker
          v-else-if="key == 'backgroundColor'"
          v-model="curComponent.style[key]"
        ></el-color-picker>
        <el-select
          v-else-if="key == 'textAlign'"
          v-model="curComponent.style[key]"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>

        <el-input type="number" v-else v-model="curComponent.style[key]" />
      </el-form-item>
      <el-form-item
        label=""
        v-if="
          curComponent && $store.state.curComponent.component == 'umr-button'
        "
      >
        <el-select v-model="curComponent.ButtonType">
          <el-option
            v-for="item in ButtonTypeOptions"
            :key="item.label"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label=""
        v-if="
          curComponent && $store.state.curComponent.component == 'umr-field'
        "
      >
        <el-input
          type="textarea"
          v-model="curComponent.Umrplaceholder"
          @change="handlePlaceholder"
        />
      </el-form-item>
      <el-form-item
        label=""
        v-if="
          curComponent && $store.state.curComponent.component == 'umr-field'
        "
      >
        <el-switch
          v-model="curComponent.pswdMode"
          active-text=""
          inactive-text=""
        >
        </el-switch>
      </el-form-item>
      <el-form-item
        label=""
        v-if="curComponent && !excludes.includes(curComponent.component)"
      >
        <el-input type="textarea" v-model="curComponent.propValue" />
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      excludes: ['umr-picture'], // 
      options: [
        {
          label: '',
          value: 'left'
        },
        {
          label: '',
          value: 'center'
        },
        {
          label: '',
          value: 'right'
        }
      ],
      map: {
        left: 'x ',
        top: 'y ',
        height: '',
        width: '',
        color: '',
        backgroundColor: '',
        borderWidth: '',
        borderColor: '',
        borderRadius: '',
        fontSize: '',
        fontWeight: '',
        lineHeight: '',
        letterSpacing: '',
        textAlign: '',
        opacity: '',
        rotate: ''
      },
      ButtonTypeOptions: [
        {
          label: '',
          value: 'plain'
        },
        {
          label: '',
          value: 'round'
        },
        {
          label: '',
          value: 'buttonDisabled'
        }
      ]
    }
  },
  computed: {
    styleKeys() {
      return this.$store.state.curComponent
        ? Object.keys(this.$store.state.curComponent.style)
        : []
    },
    curComponent() {
      return this.$store.state.curComponent
    }
  },
  methods: {
    handlePlaceholder() {
      console.log(this.curComponent)
      this.$store.commit('setCurComponentData', this.curComponent)
    }
  }
}
</script>

<style lang="scss" scoped>
.attr-list {
  overflow: auto;
  padding: 20px;
  padding-top: 0;
  height: 100%;
}
</style>
